/* iPad  */
@media (min-width: 701px) and (max-width: 1024px) and (orientation: portrait) {
  body {
    background-color: #000;
    overflow: hidden;
  }

  #game-canvas {
    box-shadow: 0 -15px 15px rgba(0, 0, 0, 0.05),
      inset 0 -15px 15px rgba(0, 0, 0, 0.05), 0 15px 15px rgba(0, 0, 0, 0.3),
      inset 0 15px 15px rgba(0, 0, 0, 0.3);
    width: 90vmin;
    height: 90vmin;
    margin-bottom: 30%;
    margin-left: 0.5%;
  }

  .rules {
    width: 100%;
    padding-left: 0rem;
    line-height: 1.5;
    transform: translateX(-100%);
    transition: transform 1s ease-in-out;
  }

  .rules-img {
    display: none;
  }

  h1,
  h2 {
    color: #fff;
  }

  h1 {
    position: absolute;
    top: 23rem;
    left: 16rem;
    font-size: 40px;
    color: #f7ae15;
  }

  #score {
    color: #f7ae15;
  }

  .rules {
    background-color: #000;
    color: #fff;
    min-height: 100vh;
    width: 100vw;
  }

  p {
    top: 38rem;
    line-height: 9rem;
    padding-right: 6rem;
    font-size: 36px;
    margin-left: 20px;
  }

  #close-btn,
  .rules-btn {
    width: 20rem;
    height: 7rem;
    border-radius: 20px;
    font-size: 26px;
    top: 95rem;
    left: 12rem;
    color: #000;
  }

  .rules-btn {
    transform: rotate(0);
  }

  #sound {
    width: 7rem;
    height: 7rem;
    top: 95rem;
    left: calc(100vw * 0.75);
  }

  #restart {
    margin-left: 1rem;
    font-size: 10rem;
    font-weight: 400;
    color: #f7ae15;
  }
}
